}

:root {
  --primary-bg-light-theme: #f8f8f8;
  --primary-bg-dark-theme: #161616;
  --mobile-menu-bg-light-theme: #9bb537;
  --mobile-menu-bg-dark-theme: #050505;
  --primary-text-color-light-theme: #050505;
  --white-text-color: #f8f8f8;
  --categories-text-color-light-theme: rgba(5, 5, 5, .3);
  --search-text-color: rgba(5, 5, 5, .5);
  --recipe-text-color: rgba(5, 5, 5, .8);
  --primary-text-color-dark-theme: #fff;
  --secondary-text-color-dark-theme: rgba(255, 255, 255, .3);
  --categories-text-color-dark-theme: rgba(5, 5, 5, .5);
  --search-text-color-dark-theme: rgba(255, 255, 255, .5);
  --recipe-text-color-dark-theme: rgba(255, 255, 255, .8);
  --accent--text-color: #9bb537;
  --hero-text-color: rgba(248, 248, 248, .6);
  --hero-secondary-text-color: rgba(248, 248, 248, .2);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

li {
  margin: 0;
  padding: 0;
}

a {
  color: currentColor;
  text-decoration: none;
  list-style: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: var(--primary-text-color-light-theme);
  background-color: var(--primary-bg-light-theme);
  font-family: Inter, sans-serif;
}

.container {
  margin: 0 auto;
}

@media screen and (max-width: 767px) {
  .container {
    min-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 100px;
  }
}



























